<template>
<div>
  <el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column
      fixed
      type="index"
      label="id">
    </el-table-column>
    <el-table-column
      prop="ip"
      label="ip">
    </el-table-column>
    <el-table-column
      prop="address"
      label="">
    </el-table-column>
    <el-table-column
      prop="time"
      label=""
      sortable
    >
    </el-table-column>
  </el-table>
  <el-pagination
    background
    layout="total, sizes, prev, pager, next, jumper"
    :current-page="tablePage.pageNum"
    :page-size="tablePage.pageSize"
    :page-sizes="pageSizes"
    :total="tablePage.total"
    @size-change="handleSizeChange"
    @current-change="handlePageChange"
    v-if="isPageShow"
  />
</div>
</template>

<script>
import { GetAddress } from '@/api/address'
export default {
  name: 'MyAddress',
  data () {
    return {
      tableDataAll: [],
      tableData: [],
      tablePage: {
        pageNum: 1, // 
        pageSize: 20, // 
        total: 0 // 
      },
      pageSizes: [10, 20, 30],
      isPageShow: true
    }
  },
  created () {
    this.get_ip_address()
  },
  methods: {
    async get_ip_address () {
      const res = await GetAddress()
      this.tableDataAll = res
      this.handleGetList()
    },
    handleGetList () { // 
      this.tableData = this.tableDataAll.slice(
        (this.tablePage.pageNum - 1) * this.tablePage.pageSize
        , this.tablePage.pageNum * this.tablePage.pageSize
      ) // 
      this.tablePage.total = this.tableDataAll.length
    },
    handlePageChange (currentPage) { // 
      this.tablePage.pageNum = currentPage
      this.handleGetList()
      // 
    },
    handleSizeChange (pageSize) { // 
      this.tablePage.pageSize = pageSize
      this.handleGetList()
      // 
    }
  }
}
</script>

