<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <div>
          <span style="margin-right: 10px"> {{ver}}</span>
          <el-button icon="el-icon-s-promotion" size="mini" @click="handleOpenUrl('https://t.me/+u6gLWF0yP5NiZWQ1')">
          </el-button>
          <el-button  size="mini" @click="handleOpenUrl('https://github.com/jaaksii/sublink')">
            <span class="iconfont icon-githubb"></span> 
          </el-button>
        </div>
      </div>
      <el-tabs v-model="activeName">
        <el-tab-pane>
          <span slot="label" class="el-icon-umbrella"> </span>
          <el-radio v-model="radio1" label="1" border v-if="optionList.length!==0"></el-radio>
          <el-radio v-model="radio1" label="2" border></el-radio>
          <div style="margin-bottom: 10px"></div>
          <!--        -->
          <div v-if="radio1==='1'">
            <div style="display: flex">
              <el-select v-model="optionValue" placeholder="">
                <el-option
                  v-for="(item,index) in optionList"
                  :key="index"
                  :value="item">
                </el-option>
              </el-select>

              <rename @handleRename="handleRename" @handleDel="handleDel"></rename>
              <!--    -->
              <div v-if="optionSub!==''">
                <Createnode :optionValue="optionValue" @RefreshSub="RefreshSub"></Createnode>
              </div>
            </div>
            <div style="margin-bottom: 10px"></div>
            <Nodelist :list="this.NodeList" v-if="optionSub!==''" @RefreshSub="RefreshSub" @CopySubNode="CopySubNode"></Nodelist>
            <div style="margin-bottom: 10px"></div>
            <div v-if="optionValue!=''">
<!--            <el-tag type="info" style="margin-right: 10px">-->
<!--              -->
<!--            </el-tag>-->
              <el-collapse accordion>
                <el-collapse-item>
                  <template slot="title">
                    <el-tag type="success">
                      ()
                    </el-tag>
                  </template>
                  <div style="margin-bottom: 10px"></div>
                  <el-input
                    type="textarea"
                    placeholder=",|"
                    v-model="optionSub"
                    rows="10"
                    show-word-limit
                  />
                  <div style="margin-bottom: 10px"></div>
                  <el-button
                    round
                    @click="handleSet"
                    style="position: relative;left: 50%;transform: translate(-50%)"
                  >
                  </el-button>
                </el-collapse-item>
              </el-collapse>

            </div>

            <div style="margin-bottom: 10px"></div>
            <div style="display: flex" v-if="optionSub!==''">
            <el-tag style="margin-right: 10px"></el-tag>
            <el-select v-model="EDIT.value" placeholder="" @change="handleUrl('edit')">
              <el-option
                v-for="(item,index) in EDIT.option"
                :key="index"
                :value="item"
              >
              </el-option>
            </el-select>
            <MyClash v-if="EDIT.value==='clash'" style="margin-left: 10px"></MyClash>
              <MySurge v-if="EDIT.value==='surge'" style="margin-left: 10px"></MySurge>
            </div>
            <div style="margin-bottom: 10px"></div>
          <div v-if="optionSub!==''">
            <el-input
              type="text"
              v-model="optionUrl"
              readonly
            >
              <template slot="prepend"></template>
              <template slot="append">
                <el-button size="small" icon="el-icon-document-copy" @click="handleCopy(optionUrl)"></el-button>
                <el-button size="small" icon="iconfont icon-erweima" @click="handleOpenQr(optionUrl)">
                  
                </el-button>
              </template>
            </el-input>
            </div>
          </div>
          <!--          -->
          <div v-else>
            <el-input
              type="text"
              placeholder="(emoji)"
              v-model.trim="name"
              maxlength="20"
              show-word-limit
            />
            <div style="margin-bottom: 10px"></div>
            <el-input
              type="textarea"
              placeholder=",|"
              v-model="sub"
              rows="10"
            />
            <div style="margin-bottom: 10px"></div>
            <el-button
              round
              style="position: relative;left: 50%;transform: translate(-50%)"
              @click="handleCreate"
            >
            </el-button>
          </div>

        </el-tab-pane>
        <el-tab-pane>
          <span slot="label"><i class="el-icon-user-solid"> </i></span>
          <USER></USER>
        </el-tab-pane>
        <el-tab-pane>
          <span slot="label"><i class="el-icon-date"> </i></span>
          <MyAddress></MyAddress>
        </el-tab-pane>
      </el-tabs>
      <div style="padding-bottom: 5px"></div>
    </el-card>
<!--    -->
    <el-dialog
      title=""
      :visible.sync="isQrShow"
      width="30%"
    >
      <vue-qr
        :text="QrTest"
      ></vue-qr>
    </el-dialog>
  </div>
</template>

<script>
import { GetSubs, CreateSub, DelSub, SetSub, DecodeSub, RenameSub } from '@/api/sub'
import USER from '@/components/user'
import MyClash from '@/components/clash'
import MySurge from '@/components/surge'
import MyAddress from '@/components/address'
import Nodelist from '@/components/nodelist'
import Rename from '@/components/rename'
import Createnode from '@/components/createnode'
import VueQr from 'vue-qr'
export default {
  name: 'MyIndex',
  data () {
    return {
      activeName: '',
      name: '',
      sub: '',
      sublist: [],
      url: '',
      radio1: '2',
      list: [],
      optionValue: '',
      optionSub: '',
      optionUrl: '',
      optionList: [],
      timer: null,
      EDIT: {
        value: 'clash',
        option: ['v2ray', 'clash', 'surge']
      },
      isQrShow: false,
      QrTest: '',
      ver: process.env.VUE_APP_VER,
      NodeList: []
    }
  },
  created () {
    this.GetSubs()
  },
  watch: {
    optionValue (newValue) {
      // console.log('' + newValue)
      // console.log(this.list)
      this.NodeList = this.list.filter(item => item.name === newValue)
      // console.log(res)
      const list = this.NodeList.map(item => item.node + (item.remarks ? '|' + item.remarks : ''))
      // console.log(list.join('\n'))
      this.optionValue = newValue
      this.optionSub = list.join('\n')
      this.handleUrl('edit')
    }
  },
  methods: {
    async GetSubs () { // 
      const res = await GetSubs()
      if (res.length === 0) {
        console.log('')
      } else {
        this.list = res
        this.optionList = Array.from(new Set(this.list.map(item => item.name)))
        this.NodeList = this.list.filter(item => item.name === this.optionValue)
        const NodeList = this.NodeList.map(item => item.node + (item.remarks ? '|' + item.remarks : ''))
        // console.log(list.join('\n'))
        this.optionSub = NodeList.join('\n')
      }
      this.list.length > 0 ? this.radio1 = '1' : this.radio1 = '2'
    },
    async handleCreate () {
      if (this.sub === '' || this.name === '') return false
      await this.isSubAddress('create')
      clearTimeout(this.timer)
      this.timer = setTimeout(async () => {
        this.sublist = this.sub.split('\n')
        const { code, msg } = await CreateSub({
          name: this.name.trim(),
          node: this.sublist
        })
        this.$message({
          message: msg,
          type: code === 200 ? 'success' : 'warning'
        })
        if (code === 200) {
          await this.GetSubs() // 
          this.radio1 = '1' // 
          this.optionValue = this.name // 
          this.name = ''
          this.sub = ''
        }
      }, 1000)
    },
    async handleSet () { // 
      if (this.optionSub === '') return false
      await this.isSubAddress('edit')
      const res = this.list.find(item => item.name === this.optionValue)
      // console.log(res, res.node)
      const list = this.optionSub.split('\n')
      const { code, msg } = await SetSub({
        name: this.optionValue.trim(),
        node: res.node,
        newNode: list
      })
      this.$message({
        message: msg,
        type: code === 200 ? 'success' : 'warning'
      })
      if (code === 200) {
        // console.log('')
        // this.optionValue = ''
        this.GetSubs() // 
        // console.log(this.NodeList)
      }
    },
    handleOpenUrl (url) {
      window.open(url)
    },
    handleOpenQr (url) { // 
      this.isQrShow = true
      this.QrTest = url
      // window.open(url)
    },
    handleDel () {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }).then(async () => {
          const { code, msg } = await DelSub(this.optionValue)
          if (code === 200) {
            this.optionList = this.optionList.filter(item => item !== this.optionValue)
            this.optionValue = '' // 
          }
          this.$message({
            type: code === 200 ? 'success' : 'warning',
            message: msg
          })
          if (this.optionList.length === 0) this.radio1 = '2'
        }).catch(() => {

        })
      }, 100)
    },
    handleCopy (value) {
      this.$copyText(value)
      this.$message({
        type: 'success',
        message: ''
      })
    },
    handleUrl (value) {
      if (value === 'edit') {
        const encoder = new TextEncoder()
        const byteText = encoder.encode(this.optionValue)
        const base64Value = encodeURIComponent(btoa(String.fromCharCode.apply(null, byteText)))
        // console.log(base64Value)
        this.optionUrl = location.origin + `/sub/${this.EDIT.value}/${base64Value}`
      }
    },
    async isSubAddress (index) { // 
      // 
      let str
      if (index === 'create') {
        str = this.sub
      } else {
        str = this.optionSub
      }
      // HTTPHTTPS
      const regex = /(http|https):\/\/[^\s]+/g
      const matches = str.match(regex)
      // 
      // console.log(matches)
      if (matches === null) return false
      const { code, msg } = await DecodeSub({ urls: matches })
      if (code === 200) {
        // 
        const list = msg
        if (index === 'create') {
          for (let i = 0; i <= (list.length - 1); i++) {
            this.sub = this.sub.replace(matches[i], msg[i])
          }
        } else {
          for (let i = 0; i <= (list.length - 1); i++) {
            this.optionSub = this.optionSub.replace(matches[i], msg[i] + ' ')
          }
        }
      }
      if (code === 400) {
        alert(msg)
      }
    },
    CopySubNode (text) {
      this.handleCopy(text)
    },
    RefreshSub () {
      this.GetSubs()
    },
    async handleRename (rename) {
      if (this.optionValue !== '' && rename !== '') {
        const { code, msg } = await RenameSub(this.optionValue,
          {
            newName: rename
          }
        )
        this.$message({
          message: msg,
          type: code === 200 ? 'success' : 'warning'
        })
        if (code === 200) {
          this.GetSubs()
          this.optionValue = rename // 
        }
      }
    }
  },
  components: {
    USER,
    MyClash,
    MySurge,
    MyAddress,
    Nodelist,
    Rename,
    Createnode,
    VueQr
  }
}
</script>

<style scoped>
@import "@/assets/icon/iconfont.css";
</style>
